@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.page {
  scroll-behavior: smooth;
  font-family: Inter, sans-serif;
}

.page__body {
  color: #333;
  background: #f7f7f7;
  min-width: 320px;
}

.page__container {
  padding-inline: 30px;
}

@media (width >= 768px) {
  .page__container {
    max-width: 768px;
    margin-inline: auto;
    padding-inline: 34px;
  }
}

@media (width >= 1440px) {
  .page__container {
    max-width: 1440px;
    margin-inline: auto;
    padding-inline: 123px;
  }
}

.page__menu {
  z-index: 1;
  opacity: 0;
  pointer-events: none;
  transition: transform .5s linear;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.red {
  border: 1px solid #adff2f;
}

